import Cookie from 'easier-cookie';

class Utils {
  constructor() {
    this.toString = Object.prototype.toString;
    this.setCookie = Cookie.set;
    this.getCookie = Cookie.get;
    this.removeCookie = Cookie.remove;
  }

  buildQuery(object) {
    if (!object || !(object instanceof Object)) return '';
    let query = '?';
    for (let key in object) {
      if (!(object[key] instanceof Object)) {
        query += `${key}=${object[key].toString()}&`;
      } else {
        query += `${key}=${JSON.stringify(object[key])}&`;
      }
    }
    return query.slice(0, query.length - 1);
  }

  getQuery(name) {
    let parts = window.location.search.replace('?', '').split('&');
    let params = {};
    for (let i = 0, len = parts.length; i < len; i++) {
      let pairs = parts[i].split('=');
      params[pairs[0]] = pairs[1];
    }
    if (params[name]) {
      return params[name];
    } else {
      return '';
    }
  }

  isFunction(func) {
    return this.toString.call(func) === '[object Function]';
  }

  isEqual(a, b, aStack, bStack) {
    // ===  true  +0  -0
    if (a === b) return a !== 0 || 1 / a === 1 / b;
    // typeof null  object  null 
    if (a == null || b == null) return false;
    //  NaN
    if (a !== a) return b !== b;
    //  a  false
    let type = typeof a;
    if (type !== 'function' && type !== 'object' && typeof b !== 'object') return false;
    //  deepEq 
    return this.deepIsEqual(a, b, aStack, bStack);
  }

  deepIsEqual(a, b, aStack, bStack) {
    // a  b  [[class]]   true
    let className = this.toString.call(a);
    if (className !== this.toString.call(b)) return false;
    switch (className) {
    case '[object RegExp]':
    case '[object String]':
      return `${a}` === `${b}`;
    case '[object Number]':
      if (+a !== +a) return +b !== +b;
      return +a === 0 ? 1 / +a === 1 / b : +a === +b;
    case '[object Date]':
    case '[object Boolean]':
      return +a === +b;
    }

    let areArrays = className === '[object Array]';
    // 
    if (!areArrays) {
      // 
      if (typeof a !== 'object' || typeof b !== 'object') return false;
      let aCtor = a.constructor;
      let bCtor = b.constructor;
      // aCtor  bCtor  Object aCtor  bCtor 
      if (aCtor !== bCtor && !(this.isFunction(aCtor) && aCtor instanceof aCtor && this.isFunction(bCtor) && bCtor instanceof bCtor) && ('constructor' in a && 'constructor' in b)) {
        return false;
      }
    }

    aStack = aStack || [];
    bStack = bStack || [];
    let length = aStack.length;
    // 
    while (length--) {
      if (aStack[length] === a) {
        return bStack[length] === b;
      }
    }
    aStack.push(a);
    bStack.push(b);
    // 
    if (areArrays) {
      length = a.length;
      if (length !== b.length) return false;
      while (length--) {
        if (!this.isEqual(a[length], b[length], aStack, bStack)) return false;
      }
    } else {
      let keys = Object.keys(a);
      let key;
      length = keys.length;
      if (Object.keys(b).length !== length) return false;
      while (length--) {
        key = keys[length];
        if (!(b.hasOwnProperty(key) && this.isEqual(a[key], b[key], aStack, bStack))) return false;
      }
    }
    aStack.pop();
    bStack.pop();
    return true;
  }

  formatInnerHTML(string) {
    string = string.replace(/(\n\s*)/g, '');
    string = string.replace(/^[^\S\n]+/gm, '');
    return string;
  }
}

export default Utils;
